html, body {
  height: 99%;
  background-color: darkgray;
}

#root {
  height: 100%;
  text-size-adjust: none;
}

table {
  width: 95%;
}

td:nth-last-child(1) {
  white-space: nowrap;
}

.inner {
  max-width: 500px;
  width: 100%;
  border: 1px solid black;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  background-color: lightgrey;
}

.nav_bar {
  display:block;
  margin: 5px 5px 5px 5px;
  background-color: wheat;
  height: 30px;
}

.nav {
  float: left;
  width: 15%;
  height: 15px;
  margin: 0px 1px;
  padding: 5px 5px 5px 5px;
  border: 1px solid black;
  cursor: pointer;
  text-align: center;
}

.nav_active {
  background-color: blue;
}

.nav_inactive {
  background-color: lightblue;
}

.tab_active {
  height: 94%;
  max-height: 94%;
  overflow: auto;
}

.tab_inactive {
  display:none;
}

/* Warning Divs */
.no_break {
  display:inline;
}

.warning_ok {
  background-color: lightgreen;
  margin: 0 5px 0 5px;
}

.warning_bad {
  background-color: #f1807e;
  margin: 0 5px 0 5px;
}
